<template>
<div class="flex overflow-hidden">
    <Sidebar v-show="$route.name != 'Adminlogin'" />
    <AdminHeader v-show="$route.name != 'Adminlogin'" />

    <div class="content">

        <router-view />
    </div>
</div>
</template>

<script>
import Sidebar from '@/components/Admin/Sidebar.vue';
import AdminHeader from '@/components/Admin/Header.vue';

export default {
    name: 'AdminLayoute',
    components: {

        Sidebar,
        AdminHeader
    }
}
</script>

<style>
@import '../../assets/admin-assets/dist/css/app.css'
</style>



